import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, ViewController } from 'ionic-angular';
import { Professionals } from '../../../providers/professionals/professionals';
import { ProfessionalPaginationMapper } from '../../../models/professional.model';
import { ProfessionalDetailPage } from '../../index';
import { Item } from '../../../models/item';
import { LoadingProvider } from '../../../providers/loading/loading';

/**
 * Generated class for the ProfessionalSearchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name: 'ProfessionalSearchPage'
})
@Component({
  selector: 'page-professional-search',
  templateUrl: 'professional-search.html',
})
export class ProfessionalSearchPage {

  searching: boolean = false;

  professionalName: string;

  currentItems: ProfessionalPaginationMapper[] = [];

  professionalSelected: ProfessionalPaginationMapper;

  page: number = 0;

  q: string = '';

  constructor(public navCtrl: NavController, private viewCtrl: ViewController, private loadingPrv: LoadingProvider,
              public professionals: Professionals, public modalCtrl: ModalController) {
    this.getItems();
  }

  getItems(params?: any) {
    return new Promise((resolve) => {
      this.loadingPrv.show('Carregando profissionais...');
      this.professionals.query(params).subscribe((data: ProfessionalPaginationMapper[]) => {
        this.loadingPrv.dismiss();
        if (params && params.q) {
          this.currentItems = data;
          resolve();
          return;
        }
        this.currentItems = this.currentItems.concat(data);
        resolve();
      });
    });
  }

  getProfessionals(ev: any) {
    const val = ev.target.value;

    if (val === '') {
      this.q = '';
      this.page = 0;
      this.getItems({page: this.page, q: this.q});
      return;
    }

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '' && val.length > 3) {
      this.q = val;
      this.page = 0;
      this.getItems({page: this.page, q: this.q});
    }
  }

  /**
   * The view loaded, let's query our items for the list
   */
  ionViewDidLoad() {
  }

  /**
   * Prompt the user to add a new item. This shows our ItemCreatePage in a
   * modal and then adds the new item to our data source if the user created one.
   */
  addItem() {
    let addModal = this.modalCtrl.create('ItemCreatePage');
    addModal.onDidDismiss(item => {
      if (item) {
        this.professionals.add(item);
      }
    });

    addModal.present();
  }

  /**
   * Delete an item from the list of items.
   */
  deleteItem(item) {
    this.professionals.delete(item);
  }

  /**
   * Navigate to the detail page for this item.
   */
  openItem(item: Item) {
    this.navCtrl.push(ProfessionalDetailPage, {
      item: item
    });
  }

  doInfinite(infiniteScroll) {
    return new Promise((resolve) => {
      console.log('doInfinite');
      this.page++;

      this.getItems({page: this.page}).then(() => {
        resolve();
        infiniteScroll.complete();
      });
    });
  }

  onCancel() {
    this.page = 0;
    this.q = '';
    this.getItems();
  }

  dismiss() {
    this.viewCtrl.dismiss({ professional: this.professionalSelected });
  }

}



